import {startAnimation, endAnimation} from './ajax_animation';
const axios = require('axios');

export default class JsonToData{
  constructor(des){
    this.json = undefined;
    this.receiveDes = des;
    this.sendbody = {"id": 1}; //
  }

  gather(){
    startAnimation();  
    axios.post(this.receiveDes, this.sendbody)
      .then(response => {
        //MODIFIED:   
        let jsonStr = response.data.data;
        console.log(jsonStr);
        this.json = JSON.parse(jsonStr);
        this.load();
      })
      .catch(error => {
        console.log(error);
        endAnimation();
      })
  }
  
  load(){
    console.log(this.json);
    //TODO: $<word>$ 
    window.xs.loadData([
      this.json,
    ])

    //
    endAnimation();
  }

  setReceiveDes(des){
    this.receiveDes = des;
    return this;
  }

  reGather(){
    this.gather();
  }
}